/** @format Nabvar*/

* {
  margin: 0;
  font-family: "Noto Sans", sans-serif;
}
.header {
  height: 64px;
  background-color: #14343f;
  box-shadow: 1px 5px 4px 0 rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
  position: fixed;
  width: 100%;
  
  z-index: 3;
}
/*Propiedades de los containers*/
.div-logo {
  display: block;
  float: left;
  padding: 0;
  margin: 0;
  height: 100%;
  width: 33.33%;
  overflow:hidden ;
  outline: none;
}
.div-user {
  width: 33.33%;
  float: left;
  overflow: hidden;
  height: 64px;
 padding: auto;

}
.div-user StarButton{
  display: block;
  
}

.div-menu {
  display: block;
  width: 33.33%;
  float: right;
  height: 100%;
}









/** @format */
.starInput {
  background-color: #ffc500;
  color: #fff;
  width: auto;
  height: auto;
  border-radius: 7px;
  font-size: large;
  font-family: "Press Start 2P", cursive;
  letter-spacing: 3px;
  text-shadow: 2px -1px #5c553e;
  padding: 10px;
  box-shadow: 3px 5px #c6571b;
  border: none;
  cursor: pointer;
  outline: none;
  display: block;
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto 
}

.starInput:active {
  color: #ffc500;
  background-color: #b08d1c;
  box-shadow: 0 2px #ffc500;
  -webkit-transform: translateY(4px);
          transform: translateY(4px);
  transition-duration: 0.03s;
}

